@import url("https://use.typekit.net/phl0vkc.css");
@import url("https://use.typekit.net/phl0vkc.css");
/*

gimlet display:
300, 400, 500, 700, 800

auto pro:
300, 400, 700, 900

*/
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

body {
  background-color: var(--primary);
  min-height: 100vh;
}

ol,
ul {
  list-style: none;
}

img {
  width: 100%;
}

a {
  text-decoration: none;
  color: var(--secondary);
}

:root {
  --primary: #28292D;
  --secondary: #FFFFFF;
  --accent: #F8CA60;
  --dark-gray: #36383D;
  --green: #4BAB5E;
  --red: #CE3E3E;
  --modal-bg: #00000080;
  --fs-xxlarge: 6rem;
  --fs-xlarge: 3.75rem;
  --fs-large: 2.25rem;
  --fs-medium: 1.5rem;
  --fs-small: 1.25rem;
  --fs-xsmall: 1rem;
  --fs-xxsmall: 0.875rem;
  --fs-tiny: 0.75rem;
  --heading-font: "gimlet-display", serif;
  --paragraph-font: "auto-pro-new", sans-serif;
}

body {
  font-family: var(--paragraph-font);
  color: var(--secondary);
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--heading-font);
}

.h1,
h1 {
  font-size: var(--fs-xxlarge);
}
@media (max-width: 768px) {
  .h1,
h1 {
    font-size: var(--fs-xlarge);
  }
}

.h2,
h2 {
  font-size: var(--fs-large);
  margin-bottom: 1rem;
}

h3,
.h3 {
  font-size: var(--fs-small);
}

.p,
p {
  font-family: var(--paragraph-font);
  font-size: var(--fs-small);
}

.score {
  font-size: var(--fs-medium);
}
.score--label {
  font-size: var(--fs-tiny);
  color: var(--accent);
}

.result {
  font-size: var(--fs-xsmall);
  color: var(--accent);
}

.text-center {
  text-align: center;
}

.font-gimlet {
  font-family: "gimlet-display", serif;
}

.font-autopro {
  font-family: "auto-pro-new", sans-serif;
}

.red {
  color: var(--red);
}

.green {
  color: var(--green);
}

.btn {
  position: relative;
  font-family: var(--paragraph-font);
  font-weight: bold;
  outline: none;
  cursor: pointer;
  padding: 0.5rem 6.25rem;
  font-size: var(--fs-medium);
  transition: ease all 0.4s;
}
@media (max-width: 768px) {
  .btn {
    padding: 0.4rem 5rem;
    font-size: var(--fs-small);
  }
}
.btn--primary {
  background-color: transparent;
  color: var(--primary);
  border: 2px solid var(--accent);
  overflow: hidden;
}
.btn--primary::after {
  content: "";
  position: absolute;
  opacity: 1;
  top: 0px;
  right: -1rem;
  width: 120%;
  height: 100%;
  transform: skew(25deg);
  background-color: var(--accent);
  transition: ease all 0.4s;
  z-index: -1;
}
.btn--primary:hover {
  color: var(--accent);
  transform: scale(1.1);
}
.btn--primary:hover::after {
  width: 0%;
  opacity: 0;
}
.btn--secondary {
  background-color: transparent;
  color: var(--secondary);
  border: 2px solid var(--secondary);
  overflow: hidden;
}
.btn--secondary::after {
  content: "";
  position: absolute;
  opacity: 0;
  top: 0px;
  left: -1rem;
  width: 0%;
  height: 100%;
  transform: skew(25deg);
  transition: ease all 0.4s;
  background-color: var(--secondary);
  z-index: -1;
}
.btn--secondary:hover {
  transform: scale(1.1);
  color: var(--primary);
}
.btn--secondary:hover::after {
  width: 120%;
  opacity: 1;
}
.btn--small {
  font-size: var(--xxsmall);
  padding: 0.4rem 4rem;
}
@media (max-width: 768px) {
  .btn--small {
    padding: 0.4rem 2.25rem;
  }
}

.icon-btn {
  display: flex;
  gap: 1rem;
  align-items: center;
  position: relative;
  cursor: pointer;
  transition: all ease 0.3s;
  background-color: transparent;
  outline: none;
  border: none;
}
.icon-btn img {
  max-height: 1.5625rem;
  color: var(--accent);
}
.icon-btn p {
  color: var(--secondary);
}
.icon-btn::after {
  position: absolute;
  content: "";
  opacity: 0;
  left: 0;
  bottom: -0.25rem;
  width: 0%;
  height: 2px;
  transition: all ease 0.4s;
  background-color: var(--secondary);
}
.icon-btn:hover::after {
  opacity: 1;
  width: 100%;
}

.pagination-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: all ease 0.2s;
}
.pagination-btn img {
  max-height: 1.25rem;
}
.pagination-btn p {
  font-size: var(--fs-xsmall);
  min-width: fit-content;
}
.pagination-btn:hover {
  cursor: pointer;
  opacity: 0.5;
}

.counter-btn,
.toggle-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 7rem;
}
.counter-btn img,
.toggle-btn img {
  height: 1.25rem;
  width: auto;
  transition: ease all 0.3s;
}
.counter-btn img:hover,
.toggle-btn img:hover {
  opacity: 0.5;
  cursor: pointer;
}
.counter-btn p,
.toggle-btn p {
  color: var(--accent);
}

.back-btn {
  position: fixed;
  bottom: 1rem;
  right: 2rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  align-self: flex-end;
  transition: all ease 0.2s;
  background-color: transparent;
  outline: none;
  border: none;
}
@media (max-width: 768px) {
  .back-btn {
    right: 1rem;
  }
}
.back-btn img {
  height: 1.25rem;
  width: auto;
}
.back-btn p {
  color: var(--secondary);
}
.back-btn:hover {
  opacity: 0.5;
  cursor: pointer;
}

.startscreen {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.startscreen h1 {
  margin-bottom: 3rem;
}
@media (max-width: 768px) {
  .startscreen h1 {
    text-align: center;
    margin-bottom: 2rem;
  }
}
.startscreen__primary-btns {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2.5rem;
}
@media (max-width: 768px) {
  .startscreen__primary-btns {
    margin-bottom: 2rem;
  }
}
.startscreen__secondary-btns {
  display: flex;
  gap: 5rem;
}
@media (max-width: 768px) {
  .startscreen__secondary-btns {
    gap: 2.5rem;
  }
}
@media (max-width: 425px) {
  .startscreen__secondary-btns .icon-btn p {
    display: none;
  }
}

.question {
  display: flex;
  flex-direction: column;
  margin-top: 3.125rem;
}
.question h3 {
  margin-bottom: 1.5625rem;
}
.question__navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 3.75rem;
}
.question__hint-btn {
  display: none;
  height: fit-content;
  width: 30px;
  transition: all ease 0.2s;
}
.question__hint-btn:hover {
  transform: scale(1.15);
  cursor: pointer;
}
.question__hint-btn--used {
  opacity: 0.5;
}
.question__hint-btn--active {
  display: initial;
}
.question__hint-btn img {
  display: block;
}
.question__numbers {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 1rem;
  max-width: 65%;
}
@media (max-width: 768px) {
  .question__numbers {
    max-width: 100%;
    margin-bottom: 2rem;
  }
}
.question__number {
  font-size: var(--fs-small);
  height: 1.875rem;
  width: 1.875rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid transparent;
  transition: all ease 0.3s;
}
.question__number:hover {
  border: 2px solid var(--secondary);
  transform: scale(1.2);
  cursor: pointer;
}
.question__number--selected {
  background-color: var(--accent);
  color: var(--primary);
}
.question__answers {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-bottom: 1.25rem;
}
.question__answer {
  border: 2px solid var(--secondary);
  padding: 1rem 1.25rem;
  transition: all ease 0.3s;
}
.question__answer p {
  opacity: 0.5;
  font-size: var(--fs-xsmall);
}
.question__answer:hover {
  background-color: var(--dark-gray);
  cursor: pointer;
  transform: scale(1.05);
}
.question__answer:active {
  background-color: var(--dark-gray);
  border: 2px solid var(--accent);
  transform: scale(0.9);
  color: var(--accent);
}
.question__answer--selected {
  background-color: var(--dark-gray);
  border: 2px solid var(--accent);
  color: var(--accent);
}
.question__answer--selected p {
  opacity: 1;
}
.question__answer--selected:hover {
  color: var(--accent);
}
.question__answer--disabled {
  pointer-events: none;
  background-color: var(--dark-gray);
  border: none;
}
.question__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 425px) {
  .question__controls {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
}
.question__pagination {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.settings {
  display: flex;
  flex-direction: column;
}
.settings__list {
  min-height: 18.75rem;
  border: 2px solid var(--secondary);
  padding: 1.5rem 2rem;
  margin-bottom: 1.25rem;
}
@media (max-width: 768px) {
  .settings__list {
    padding: 1.5rem;
  }
}
.settings__general {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  border-bottom: 2px solid var(--secondary);
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
}
.settings__general li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .settings__general li {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 0.5rem;
  }
}
@media (max-width: 425px) {
  .settings__general li {
    align-items: center;
    text-align: center;
  }
}
.settings__difficulty p {
  margin-bottom: 0.75rem;
}
@media (max-width: 425px) {
  .settings__difficulty p {
    text-align: center;
  }
}
.settings__difficulty-btns {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  border-bottom: 2px solid var(--secondary);
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
}
@media (max-width: 768px) {
  .settings__difficulty-btns {
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
  }
}
@media (max-width: 768px) {
  .settings__difficulty-btns button {
    width: 100%;
  }
}
.settings__tags-search-form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-bottom: 0.5rem;
}
.settings__tags-search-form input {
  padding-left: 0.5rem;
  width: 50%;
  background-color: var(--dark-gray);
  border: none;
  outline: none;
  font-size: var(--fs-small);
  color: var(--secondary);
  font-family: var(--paragraph-font);
}
.settings__tags-search-form input::placeholder {
  font-weight: bold;
}
.settings__input-container {
  position: relative;
  display: flex;
  align-items: center;
  background-color: var(--dark-gray);
  padding: 0.5rem;
}
.settings__selected-tags {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  justify-content: flex-end;
  width: 50%;
  flex-wrap: wrap;
}
.settings__selected-tag {
  display: flex;
  align-items: center;
  background-color: var(--accent);
  color: var(--dark-gray);
  font-size: var(--fs-small);
  gap: 1rem;
  padding: 0 0.75rem;
  transition: all ease 0.3s;
}
.settings__selected-tag:hover {
  cursor: pointer;
  opacity: 0.5;
}
.settings__selected-tag img {
  width: 12px;
  height: 12px;
}
.settings__tag-results {
  position: absolute;
  background-color: var(--dark-gray);
  padding: 0.5rem;
  display: none;
  flex-direction: column;
  width: fit-content;
  z-index: 999;
}
.settings__tag-result {
  font-size: var(--fs-small);
  padding: 0.25rem 1.5rem 0.25rem 0.75rem;
}
.settings__tag-result:hover {
  background-color: var(--primary);
  cursor: pointer;
}
.settings__controls {
  display: flex;
  justify-content: flex-end;
  gap: 1.5rem;
  margin-bottom: 0.75rem;
}
@media (max-width: 768px) {
  .settings__controls {
    gap: 0.75rem;
  }
}
@media (max-width: 768px) {
  .settings__controls button {
    width: 100%;
  }
}
.settings__errors {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.settings__errors p {
  display: none;
}

.highscores {
  display: flex;
  flex-direction: column;
}
.highscores__list {
  border-top: 2px solid var(--secondary);
  border-bottom: 2px solid var(--secondary);
  min-height: 14.75rem;
  margin-bottom: 1.25rem;
  padding-top: 1.25rem;
}
.highscores__entry {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  width: 100%;
  gap: 2rem;
  cursor: pointer;
  border: 2px solid transparent;
  transition: all ease 0.3s;
}
.highscores__entry--champion {
  border: 2px solid var(--secondary);
  background-color: var(--dark-gray);
}
.highscores__entry:hover {
  transform: scale(1.05);
}
.highscores__entry-user {
  display: flex;
  align-items: center;
  gap: 1.25rem;
  width: 100%;
}
.highscores__entry-user img {
  max-height: 1.5rem;
  width: auto;
  margin-bottom: 0.125rem;
}
.highscores__entry-points {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.results {
  display: flex;
  flex-direction: column;
}
.results__title-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
  margin-bottom: 1rem;
}
.results__title-container h2 {
  margin: 0;
}
.results__title-container p {
  font-size: var(--fs-small);
  color: var(--accent);
}
.results__title-container span {
  font-size: var(--fs-large);
  color: var(--secondary);
  margin-right: 0.5rem;
}
.results__info-container {
  width: 100%;
  border-top: 2px solid var(--secondary);
  padding: 1rem 2rem;
  background-color: var(--dark-gray);
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
@media (max-width: 768px) {
  .results__info-container {
    flex-direction: row-reverse;
  }
}
@media (max-width: 425px) {
  .results__info-container {
    flex-direction: column;
    align-items: center;
  }
}
.results__graph {
  height: 100%;
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .results__graph {
    width: 40%;
  }
}
.results__general-info {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 768px) {
  .results__general-info {
    width: 60%;
    flex-direction: column;
  }
}
@media (max-width: 425px) {
  .results__general-info {
    width: 100%;
  }
}
.results__infopiece {
  width: 50%;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
@media (max-width: 768px) {
  .results__infopiece {
    width: 100%;
  }
}
@media (max-width: 425px) {
  .results__infopiece {
    justify-content: center;
  }
}
.results__save-score {
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.results__save-score h3 {
  font-size: var(--fs-medium);
}
.results__answer-comparison {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.results__question {
  display: flex;
  flex-direction: column;
}
.results__question--wrong .results__question-icon {
  background-color: var(--accent);
  padding: 1rem;
}
.results__question--wrong .results__answer-info {
  display: flex;
}
.results__question:hover {
  cursor: pointer;
}
.results__question:hover .results__question-icon {
  transform: scale(1.1);
}
.results__question:hover p {
  opacity: 1;
}
.results__question p {
  opacity: 0.5;
  transition: all ease 0.2s;
}
.results__question-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--dark-gray);
  min-height: 60px;
}
.results__question-info p {
  padding: 0.75rem 2rem;
  max-width: 40rem;
}
.results__answer-info {
  margin-top: 1rem;
  display: none;
  flex-direction: column;
  border: solid 3px var(--dark-gray);
  padding: 1rem 2rem;
}
.results__question-icon {
  align-self: flex-start;
  height: 60px;
  width: 60px;
  min-width: 60px;
  padding: 0.75rem;
  border: solid 3px var(--accent);
  transition: all ease 0.2s;
}

.circle-graph {
  position: relative;
  width: 90px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background: var(--primary);
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .circle-graph {
    min-width: 120px;
    width: 70%;
  }
}
.circle-graph::before {
  position: absolute;
  content: "";
  width: 80%;
  height: 80%;
  border-radius: 50%;
  background-color: var(--dark-gray);
}
.circle-graph__content {
  position: relative;
  margin: 0;
}

.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: var(--modal-bg);
}
.modal--hidden {
  display: none;
}
.modal__form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
  width: 100%;
}
.modal__form input {
  padding: 0.5rem 1rem;
  width: 100%;
  background-color: var(--dark-gray);
  border: none;
  outline: none;
  font-size: var(--fs-small);
  color: var(--secondary);
  font-family: var(--paragraph-font);
}
.modal__form input::placeholder {
  font-weight: bold;
}
.modal__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  background-color: var(--primary);
  padding: 2.5rem;
}
.modal__buttons {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

main::after {
  content: "";
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 0.3125rem;
  background-color: var(--secondary);
}

.container {
  margin: 0 auto;
  max-width: 50rem;
  width: 100%;
  padding: 3.125rem 2rem 4rem;
}
@media (max-width: 768px) {
  .container {
    padding: 2rem 1rem 8rem;
  }
}
.container--inactive {
  display: none;
}

body::-webkit-scrollbar-track {
  background-color: var(--dark-gray);
}
body::-webkit-scrollbar {
  width: 0.75rem;
  background-color: var(--dark-gray);
}
body::-webkit-scrollbar-thumb {
  background-color: var(--accent);
}