@use "../abstracts/" as *;

*,
*::after,
*::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-size: 100%;
}

body {
    background-color: var(--primary);
    min-height: 100vh;
}

ol,
ul {
    list-style: none;
}

img {
    width: 100%;
}

a {
    text-decoration: none;
    color: var(--secondary);
}
